.skillBlock {
    width: 250px;
    background-color: #7878e5;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    box-sizing: border-box;
}

.icon  {
    width: 60px;
    height: 60px;
    background-color: #c9c8c8;
}

@media (max-width: 360px) {
    .skillBlock {
        padding: 10px;
    }

}
